<template>
  <v-app>
    <v-navigation-drawer v-model="sidebar" app temporary class='mt-5' style='top: 60px' right color="#000">
      <v-list>
        <v-list-item v-for="item in menuItems" :key="item.title" :to="item.path">
          <v-list-item-action>
            <v-icon>{{ item.icon }}</v-icon>
          </v-list-item-action>
          <v-list-item-content class="list-item-content">{{ item.title }}</v-list-item-content>
        </v-list-item>
      </v-list>
    </v-navigation-drawer>

    <v-app-bar 
      flat 
      dark
      dense 
      app 
      class="pl-5 pr-5"
      height="80"
    >
      <router-link to="/">
          <img
            src="@/assets/logo.png"
            alt="Sthapotik Nirman"
            class="mx-2 hidden-sm-and-down"
            max-width="40"
            max-height="40"
          />
          </router-link>
          <router-link to="/">
          <img
            src="@/assets/logo.png"
            alt="Sthapotik Nirman"
            class="mx-2 hidden-md-and-up"
            max-width="30"
            max-height="30"
          />
          </router-link>
        <router-link to="/" style="text-decoration:none; color: #fff;">
      <v-toolbar-title>
          STHAPOTIK NIRMAN
      </v-toolbar-title>
        </router-link>
      <v-spacer></v-spacer>
      <!-- <p class="hidden-sm-and-down" style="margin-bottom: 0px;">
        We are a team working on architectural design,
        <br>
        interior design and related engineering design
        <br>
        consultancy service with top supervision
      </p> -->
      <span class="who-we-are-appbar hidden-sm-and-down">
        We are a team working on <span class="highlight-appbar">architectural design,<br></span><span class="highlight-appbar spacing-design-appbar"> interior design</span> and related <span class="highlight-appbar"> engineering design</span><br> consultancy service with <span class="highlight-appbar"> top supervision</span>
      </span>
      <v-spacer></v-spacer>
      <v-toolbar-items class="hidden-xs-only">
        <!-- <v-btn text v-for="item in menuItems" :key="item.title" :to="item.path">
          {{ item.title }}
        </v-btn> -->
        <v-btn text to="/">Home</v-btn>
        <v-menu
          open-on-hover
          offset-y
          transition="scale-transition"
        >
          <template v-slot:activator="{ on, attrs }">
            <v-btn text to="/portfolio" v-bind="attrs" v-on="on">Portfolio</v-btn>
          </template>
          <v-list class="category-list">
            <v-list-item
              class="category-items"
              v-for="project_category in categorywise_data"
              :key="project_category.id"
              @click.native="getProjects(project_category.id)"
            >
              <v-list-item-title class="category-item-title">{{ project_category.name }}</v-list-item-title>
            </v-list-item>
          </v-list>
        </v-menu>
        <v-btn text to="/contact">Contact</v-btn>
        <v-btn text to="/about-us">About Us</v-btn>
      </v-toolbar-items>
      <span class="hidden-sm-and-up">
        <v-app-bar-nav-icon @click="sidebar = !sidebar"></v-app-bar-nav-icon>
      </span>
    </v-app-bar>
    <!-- <main-nav/> -->
    <!-- <profile-right-nav /> -->
    <v-main>
      <!-- <ProfilePage/> -->
      <!-- <HomePage/> -->
      <router-view/>

      <vue-progress-bar></vue-progress-bar>
    </v-main>
      <footer-section/>
  </v-app>
</template>

<script>
import FooterSection from './components/FooterSection'

export default {
  name: 'App',

  components: {
        FooterSection
  },

  data: () => ({
    sidebar: false,
    menuItems:[
      {title: 'Home', path:'/'},
      {title: 'Portfolio', path:'/portfolio'},
      {title: 'Contact', path:'/contact'},
      {title: 'About Us', path:'/about-us'},
    ],
    categorywise_data: "",
  }),

  async created(){
      await this.axios.get(process.env.VUE_APP_API_URL+'/project-category').then((response)=> {
          this.categorywise_data = response.data;
      })
      .catch(error => {
        console.log(error)
      });
  },

  methods:{
      getProjects(categoryId){
          this.$router.push({ path: `/projects/${categoryId}` }).catch(err => {
                    // Ignore the vuex err regarding  navigating to the page they are already on.
                    if (
                    err.name !== 'NavigationDuplicated' &&
                    !err.message.includes('Avoided redundant navigation to current location')
                    ) {
                    // But print any other errors to the console
                    console.log(err);
                    }
                });
      }
  }
};
</script>

<style>
.theme--dark.v-app-bar.v-toolbar.v-sheet {
    background-color: #000 !important;
}
.v-card{
    cursor:pointer;
}
.category-list{
  background-color:#000 !important;
  /* color: black; */
}
.category-item-title{
  color: white;
}
.category-items:hover{
  /* background: #ffc13b; */
  background: #FFFF00;
  cursor: pointer;
}

.category-items:hover .category-item-title{
  color: black;
}

a.v-btn--active.v-btn--router{
  /* background-color:#ffc13b !important; */
  background-color:#FFFF00 !important;
  /* color: black; */
}
a.v-btn--active.v-btn--router .v-btn__content{
  color: #000;
}

a.v-btn.v-btn--router:after {
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 100%;
    transform: scaleY(0);
    transform-origin: top center;
    background: #FFFF00;
    z-index: -1;
    transition: transform 0.5s;
}

a.v-btn.v-btn--router:hover::after{
  transform: scaleY(1);
}

a.v-btn.v-btn--router:hover .v-btn__content{
  color: #000;
}

.v-list .v-list-item--active {
  background-color:#FFFF00 !important;
  /* color: black !important; */
}

.v-toolbar__title{
  font-family: calibri;
}

.v-list .v-list-item .v-list-item__content{
  color: #fff;
}

.v-list .v-list-item--active .v-list-item__content{
  color: #000;
}

.theme-separator-line-horrizontal-full {
    background-color: #FFFF00;
    width: 100%;
    height: 0.25rem;
    width: 5.25rem;
    float: none;
    margin-left: auto;
    margin-right: auto;
    margin-top: 0.938rem;
}

.section-title{
    margin: 0rem;
    font-size: 1.8rem;
    line-height: 1.3;
    font-weight: 300;
    text-transform: uppercase;
}

.card-hover {
  transition: transform 1s;
}

.card-hover:hover {
  transform: translate(0px, -20px);
}

.v-card--reveal {
    align-items: center;
    bottom: 0;
    justify-content: center;
    opacity: .5;
    position: absolute;
    width: 100%;
}

.v-application--wrap {
    min-height: 100vh;
    justify-content: center;
}
.who-we-are-appbar{
  font-size: 17px;
  line-height: 1.2em;
}

.highlight-appbar{
  font-size: 21px;
  font-weight: 500;
}

@media only screen and (min-width: 960px) {
  .spacing-design-appbar{
    margin-left: 70px;
  }
}

::-webkit-scrollbar{
  display: none;
}

*{
  -ms-overflow-style: none;
  scrollbar-width: none;
}

.mdi-close-thick::before{
  font-size: 26px;
}

.v-dialog:not(.v-dialog--fullscreen){
  max-height: 82% !important;
}
</style>
