                                                    <v-list-item-subtitle>
                                                    {{ detailsModal.no_of_storey }}
                                                    </v-list-item-subtitle>
                                                </v-list-item-content>
                                            </v-list-item>
                                        </v-list> -->
                                        <v-list v-if="detailsModal.land_area !== null">
