                                                <v-list-item-icon>
                                                    <v-icon>mdi-account-cowboy-hat</v-icon>
                                                </v-list-item-icon>
                                                <v-list-item-content>
                                                    <v-list-item-title>Principle Architect</v-list-item-title>
                                                    <v-list-item-subtitle>
                                                    {{ detailsModal.main_architect }}
                                                    </v-list-item-subtitle>
                                                </v-list-item-content>
                                            </v-list-item>
                                        </v-list> -->
                                        <!-- <v-list class="team_members_title">
                                            <v-list-item>
                                                <v-list-item-icon>
                                                    <v-icon>mdi-account-group-outline</v-icon>
                                                </v-list-item-icon>
                                                <v-list-item-content>
                                                    <v-list-item-title>Team Members</v-list-item-title>
                                                </v-list-item-content>
                                            </v-list-item>
                                        </v-list>
                                        <v-list class="team_members">
                                            <v-list-item
                                            v-for="(item, i) in detailsModal.team_members"
                                            :key="i"
                                            >
                                                <v-list-item-icon>
                                                    <v-icon></v-icon>
                                                </v-list-item-icon>
                                                <v-list-item-content class="team_members_content">
                                                    <v-list-item-subtitle>
                                                        {{ item }}
                                                    </v-list-item-subtitle>
                                                </v-list-item-content>
                                            </v-list-item>
                                        </v-list> -->
                                    </v-col>
                                </v-row>
                            </v-layout>
                        </v-container>
                    </v-card-text>
                </div>
            </v-card>
        </v-dialog>
        <v-dialog v-model="dialogImage" max-width="100vh">
            <v-card color="transparent" width="100vh" height="100vh">
                <v-img :src="singleImage" width="100%" height="100%">
                    <v-row>
                        <v-col class="text-right">
                            <v-btn
                            icon
                            x-large
                            @click="dialogImage = false;"
                            color="#FFFF00"
                            >
                                <v-icon>mdi-close-thick</v-icon>
                            </v-btn>
                        </v-col>
                    </v-row>
                </v-img>
            </v-card>
        </v-dialog>
    </v-container>
</template>

<script>
    export default {
        data: () => ({
            dialog: false,
            dialogImage: false,
            detailsModal : "",
            singleImage: "",
            projects: "",
            project_category: "",
            image_base_url: ""
        }),
        watch: {
            '$route' (to, from) {
                this.$Progress.start()
                this.axios.get(process.env.VUE_APP_API_URL+'/project-category/'+to.params.id).then((response)=> {
                    this.projects = response.data;
                    this.projects.map((item) => {
                        this.project_category = item.project_category.name;
                    })
                    this.$Progress.finish()
                })
                .catch(error => {
                    console.log(error)
                });
            }
        },
        mounted () {
    //    [App.vue specific] When App.vue is finish loading finish the progress bar
            this.$Progress.finish()
        },
        async created(){
            this.$Progress.start()
                await this.axios.get(process.env.VUE_APP_API_URL+'/project-category/'+this.$route.params.id).then((response)=> {
                    this.projects = response.data;
                    this.projects.map((item) => {
                        this.project_category = item.project_category.name;
                    })
                })
                .catch(error => {
                    console.log(error)
                });
            this.image_base_url = process.env.VUE_APP_IMG_URL;
        },
        methods: {
        OpenDialogBox(item){
            this.detailsModal = item;
            this.dialog = true;
        },
        getId(image){
            this.singleImage = this.image_base_url+image.image_path
            this.dialogImage = true
        }
    }
    }
</script>

<style lang="scss" scoped>
.card-hover {
  transition: transform 0.5s;
}

.card-hover:hover {
  transform: translate(0px, -20px);
}

.v-list .v-list-item .v-list-item__content{
  color: #000 !important;
}
@media only screen and (min-device-width: 768px){

    .project-modal-title {
        color: rgba(211,211,211,0.8);
        position: absolute;
        font-size:20px;
        line-height: 1em;
        letter-spacing: -.8px;
        top: 85%;
        left: 70%;
        transform: translate(-25%, -25%);
    }
}
@media only screen and (max-device-width: 768px){
    .project-modal-title {
        color: rgba(211,211,211,0.8);
        position: absolute;
        font-size:20px;
        line-height: 1em;
        letter-spacing: -.8px;
        top: 80%;
        left: 70%;
        transform: translate(-40%, -40%);
    }
}
</style>