<template>
    <v-container>
            <v-layout class="pa-5">
                <v-flex text-center>
                    <!-- <div class="section-subtitle title">Our Works</div> -->
                    <h4 class="section-title">Our Projects</h4>
                    <div class="theme-separator-line-horrizontal-full"></div>
                </v-flex>
            </v-layout>
        <v-container fluid>     
            <v-row class="mt-5">
                <v-col v-for="project_category in categorywise_data" :key="project_category.id">
                        <v-card elevation="12" class="mx-auto card-hover" max-width="344" @click.native="getProjects(project_category.id)">
                            <v-img 
                            height="280" 
                            :src="image_base_url+project_category.image_path"
                            lazy-src="@/assets/placeholder.jpg"
                            >
                                <template v-slot:placeholder>
                                    <v-row
                                        class="fill-height ma-0"
                                        align="center"
                                        justify="center"
                                    >
                                        <v-progress-circular
                                        indeterminate
                                        color="grey lighten-5"
                                        ></v-progress-circular>
                                    </v-row>
                                </template>
                            </v-img>
                            <v-card-title>
                                {{ project_category.name }}
                            </v-card-title>
                        </v-card>
                </v-col>
            </v-row>
        </v-container>
    </v-container>
</template>

<script>
    export default {
        data: () => ({
            categorywise_data: "",
            image_base_url: ""
        }),
        mounted () {
    //    [App.vue specific] When App.vue is finish loading finish the progress bar
            this.$Progress.finish()
        },
        async created(){
            this.$Progress.start()
            
            await this.axios.get(process.env.VUE_APP_API_URL+'/project-category').then((response)=> {
                this.categorywise_data = response.data;
            })
            .catch(error => {
                console.log(error)
            });
            this.image_base_url = process.env.VUE_APP_IMG_URL;
        },

        methods:{
            getProjects(categoryId){
                this.$router.push({ path: `/projects/${categoryId}` }).catch(err => {
                    // Ignore the vuex err regarding  navigating to the page they are already on.
                    if (
                    err.name !== 'NavigationDuplicated' &&
                    !err.message.includes('Avoided redundant navigation to current location')
                    ) {
                    // But print any other errors to the console
                    console.log(err);
                    }
                });
            }
        }
    }
</script>

<style lang="scss" scoped>
.card-hover {
  transition: transform 0.5s;
}

.card-hover:hover {
  transform: translate(0px, -20px);
}
</style>